import React from 'react';
import './Main.css';
import image from './images/circle.png';

export class ClickDetector extends React.Component {

    constructor(props) {
        super(props);

        this.setWrapperRef = this.setWrapperRef.bind(this);
        this.handleClickOutside = this.handleClickOutside.bind(this);

        this.state = {
            top: [0],
            left: [0],
            index: [0],
        }
    }

    componentDidMount() {
        document.addEventListener('mousedown', this.handleClickOutside);
    }

    componentWillUnmount() {
        document.removeEventListener('mousedown', this.handleClickOutside);
    }

    setWrapperRef(node) {
        this.wrapperRef = node;
    }

    handleClickOutside(event) {
        if (this.wrapperRef && !this.wrapperRef.contains(event.target)) {
            this.setState({
                top: [...this.state.top, event.clientY-(115/2)],
                left: [...this.state.left, event.clientX-(115/2)],
                index: [...this.state.index, this.state.index.length],
            });
        }
    }

    render() {
        const style = [];
        for (var i = 0; i < this.state.index.length; i++) {
            style.push({
                top: this.state.top[i] + 'px',
                left: this.state.left[i] + 'px',
            });
            // document.querySelector('#drop').style.setProperty('--top',this.state.top[i] + 'px');
        }
        const listItems = this.state.index.map((number) =>
            <image src={image} id="drop" style={style[number]} ref={this.setWrapperRef} key={number.toString()}>
            </image>
        );
        return listItems;
    }
}